.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Monserrat", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  font-family: "Monserrat", sans-serif;
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Monserrat", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.boton-azul-block {
  background-color: #24243c;
  color: #FFFFFF;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  padding: 1rem 4rem;
  margin-top: 3rem;
  display: block;
  border: none;
}
.boton-azul-block:hover {
  background-color: #1a1a2c;
  cursor: pointer;
}

.boton-azul-inline {
  background-color: #24243c;
  color: #FFFFFF;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  padding: 1rem 4rem;
  margin-top: 3rem;
  display: inline-block;
  border: none;
}
.boton-azul-inline:hover {
  background-color: #1a1a2c;
  cursor: pointer;
}

.boton-tinto {
  background-color: #ae2f3c;
  color: #FFFFFF;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  padding: 1rem 4rem;
  margin-top: 3rem;
  display: inline-block;
  border: none;
}
.boton-tinto:hover {
  background-color: #9a2a35;
  cursor: pointer;
}

.dark-mode {
  filter: invert(100);
  width: 2.5rem;
  height: 2.5rem;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .dark-mode {
    margin: 0 0 0 3rem 0;
  }
}
.dark-mode:hover {
  cursor: pointer;
  height: 3rem;
  transition: height 0.1s ease-in;
}

.oscuro {
  background-color: #141414;
}
.oscuro p, .oscuro h1, .oscuro h2, .oscuro h3, .oscuro h4, .oscuro blockquote, .oscuro label {
  color: #FFFFFF;
}
.oscuro .anuncio {
  background-color: #ffc8a2;
  border-color: #ffaa6f;
}
.oscuro .anuncio p, .oscuro .anuncio h3 {
  color: #000000;
}
.oscuro .anuncio .precio {
  color: #ae2f3c;
}
.oscuro .ubicacion h3, .oscuro .ubicacion p {
  color: #000000;
}
.oscuro fieldset p, .oscuro fieldset legend {
  color: #FFFFFF;
}

/* UI */
.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #1c0906;
  padding: 1rem 0 4rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
.header.inicio .contenido-header {
  min-height: 70rem;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  max-width: 90rem;
  line-height: 1.5;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.logo-header {
  display: flex;
  justify-content: center;
  align-items: center;
}
.logo-header img {
  margin-right: 3rem;
  max-width: 6.5rem;
  max-height: 5.5rem;
  margin-bottom: 1rem;
}
.logo-header p {
  font-size: 2.3rem;
  font-weight: 700;
  color: #FFFFFF;
  text-transform: uppercase;
}

.navegacion {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.7s ease-out, opacity 0.7s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    flex-direction: row;
    align-items: center;
    visibility: visible;
    opacity: 1;
  }
}
.navegacion a {
  text-decoration: none;
  color: #FFFFFF;
  text-align: center;
  display: block;
  font-size: 2.2rem;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}
.navegacion a:hover {
  color: #2bca9b;
  transition: ease 0.2s all;
  font-size: 2.3rem;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 7rem;
  margin: 1rem 0 1rem 0;
}

.mostrar {
  visibility: visible;
  opacity: 1;
}

.footer {
  padding: 2.5rem 0;
  background-color: #1c0906;
  margin-bottom: 0;
}
.footer .contenido-footer {
  padding-top: 2.5rem;
}
.footer .contenido-footer p {
  color: #FFFFFF;
}
.footer .copyright {
  text-align: center;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
}

.redes-sociales {
  display: flex;
  align-items: center;
  justify-content: center;
}
.redes-sociales img {
  width: 5rem;
  margin-right: 0.5rem;
}
.redes-sociales a {
  display: flex;
  justify-content: center;
  margin-right: 5rem;
}
.redes-sociales a:last-of-type {
  margin-right: 0;
}

.facebook,
.instagram {
  font-size: 1.5rem;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.iconos img {
  height: 10rem;
}
.iconos h3 {
  text-transform: uppercase;
  font-weight: 700;
}
.iconos p {
  text-align: center;
}

@media (min-width: 480px) {
  .contenedor-anuncio {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .contenedor-anuncio {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.anuncio {
  margin-top: 3rem;
  background-color: #FFF5EE;
  border: 2px solid #ffe9da;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .anuncio {
    margin-top: 0;
  }
}
.anuncio picture {
  height: auto;
  overflow: hidden;
}
@media (min-width: 768px) {
  .anuncio picture {
    height: 33.5rem;
  }
}
@media (min-width: 1024px) {
  .anuncio picture {
    max-height: 36.5rem;
  }
}

.contenido-anuncio {
  padding: 3rem;
}
.contenido-anuncio h3, .contenido-anuncio p {
  margin: 0;
  overflow: hidden;
}
.contenido-anuncio h3 {
  font-size: 2.3rem;
  font-weight: 700;
  margin-bottom: 2rem;
}
.contenido-anuncio .precio {
  color: #ae2f3c;
  font-weight: 700;
  font-size: 2.5rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.iconos-caracteristicas img {
  flex: 0 0 3rem;
}
.iconos-caracteristicas p {
  font-weight: 700;
}

.webp .imagen-contacto {
  background-image: url(../img/contactar.webp);
}

.no-webp .imagen-contacto {
  background-image: url(../img/contactar.jpg);
}

.imagen-contacto {
  margin-top: 2rem;
  background-position: center center;
  background-size: cover;
  padding: 10rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  color: #FFFFFF;
  font-weight: 700;
  font-size: 4rem;
}
.imagen-contacto p {
  color: #FFFFFF;
  text-align: center;
  font-size: 1.8rem;
}

@media (min-width: 1024px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  align-items: center;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .entrada-blog img {
    height: 20rem;
    width: 30rem;
  }
}

.texto-entrada a {
  color: #000000;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
  font-size: 2.3rem;
}
.texto-entrada h4::after {
  content: "";
  width: 35rem;
  height: 0.5rem;
  display: block;
  background-color: #2bca9b;
  margin-top: 1rem;
}
.texto-entrada span {
  color: #ae2f3c;
  font-weight: 700;
}
.texto-entrada p {
  margin: 1rem 0;
}

.testimonial {
  background-color: #2bca9b;
  padding: 2rem;
  font-size: 2.4rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../../../build/img/comilla.svg);
  background-repeat: no-repeat;
  display: block;
  width: 6rem;
  height: 8rem;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
  font-weight: 700;
}

.ubicacion {
  background-color: #FFF5EE;
  padding: 3rem 0;
  border: 1px solid #ffe9da;
  margin: 5rem 1rem 0 1rem;
}
.ubicacion h3 {
  margin-top: 0;
}
.ubicacion p {
  margin: 5rem 0 0 0;
  text-align: center;
  font-size: 1.5rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .ubicacion p {
    text-align: right;
  }
}
@media (min-width: 1300px) {
  .ubicacion p {
    text-align: center;
  }
}

.contenedor-ubicacion {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 1rem;
}

#map {
  z-index: 0;
  width: 130rem;
  height: 50rem;
}

/* Internas */
@media (min-width: 1024px) {
  .contenido-nosotros {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  margin-top: 5rem;
  font-weight: 700;
  font-size: 2.2rem;
}
@media (min-width: 1024px) {
  .texto-nosotros blockquote {
    margin-top: 0;
  }
}

.imagen-nosotros {
  display: flex;
  align-items: center;
  justify-content: center;
}
.imagen-nosotros picture {
  box-shadow: -10px 10px 25px -1px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: -10px 10px 25px -1px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -10px 10px 25px -1px rgba(0, 0, 0, 0.75);
}

.imagen-formulario {
  overflow: hidden;
}
@media (min-width: 768px) {
  .imagen-formulario {
    height: 35rem;
  }
}
@media (min-width: 1024px) {
  .imagen-formulario {
    height: 55rem;
  }
}

.formulario fieldset {
  border-radius: 1rem;
}
.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  text-transform: uppercase;
  font-weight: 700;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea {
  padding: 1rem;
  width: 100%;
  display: block;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
  background-color: #FFFFFF;
}

.form-radio {
  max-width: 45rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form-radio input[type=radio] {
  margin: 0;
  width: auto;
}